"use client";

interface Metric {
  _id: string;
  key: string;
  value: number;
  updatedAt: number;
}

interface Task {
  status: string;
}

function formatValue(key: string, value: number): string {
  if (key === "revenue") {
    return `$${(value / 1000).toFixed(0)}K`;
  }
  return value.toLocaleString();
}

function getLabel(key: string): string {
  const labels: Record<string, string> = {
    revenue: "Revenue",
    contacts: "GHL Contacts",
    deals_in_contract: "Deals Active",
    properties_tracked: "Properties",
  };
  return labels[key] || key;
}

export function MetricsBar({
  metrics,
  tasks,
}: {
  metrics: Metric[];
  tasks: Task[];
}) {
  const completedTasks = tasks.filter((t) => t.status === "done").length;
  const inProgressTasks = tasks.filter((t) => t.status === "in_progress").length;

  return (
    <div className="border-t border-[#1e1e1e] px-4 py-3 bg-[#0a0a0a]">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-6">
          {metrics.map((metric) => (
            <div key={metric._id} className="flex items-center gap-2">
              <span className="font-mono text-[10px] text-[#525252] uppercase tracking-wider">
                {getLabel(metric.key)}
              </span>
              <span className="font-mono text-sm text-emerald-500 font-medium">
                {formatValue(metric.key, metric.value)}
              </span>
            </div>
          ))}
        </div>

        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2">
            <span className="font-mono text-[10px] text-[#525252] uppercase tracking-wider">
              Tasks
            </span>
            <span className="font-mono text-xs text-[#737373]">
              {inProgressTasks} active / {completedTasks} done
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}
